@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@500&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*,*:before,*:after{margin:0;box-sizing:border-box}a,button{cursor:pointer}a{text-decoration:none}.btn,a{transition:all 0.2s ease-in-out;-webkit-transition:all 0.2s ease-in-out}.btn:active,.btn:focus,.btn:hover,a:active,a:focus,a:hover{outline:0;box-shadow:none}input[type="email"],input[type="password"],input[type="text"]{-webkit-appearance:none;-moz-appearance:none;appearance:none;-webkit-focus-ring-color:none;outline:none}input[type="email"]:active,input[type="email"]:focus,input[type="email"]:hover,input[type="password"]:active,input[type="password"]:focus,input[type="password"]:hover,input[type="text"]:active,input[type="text"]:focus,input[type="text"]:hover{box-shadow:none}input,select,button,textarea{outline:none;-webkit-appearance:none;-moz-appearance:none;appearance:none}input::-webkit-outer-spin-button,input::-webkit-inner-spin-button,select::-webkit-outer-spin-button,select::-webkit-inner-spin-button,button::-webkit-outer-spin-button,button::-webkit-inner-spin-button,textarea::-webkit-outer-spin-button,textarea::-webkit-inner-spin-button{-webkit-appearance:none;-moz-appearance:none;appearance:none}body{color:#222;font:normal 125%/1.4 "Helvetica Neue Light","Helvetica Neue","Helvetica","Arial",sans-serif}header{position:fixed;top:0;left:0;display:flex;justify-content:center;align-items:center;width:100%;height:60px;text-align:center}footer{position:fixed;bottom:0;left:0;display:flex;justify-content:center;align-items:center;width:100%;height:60px;text-align:center}.home{background-color:#2e2e1e;z-index:-2;display:flex;align-items:center;justify-content:center;height:100vh;width:100vw}.home .text-holder{position:relative;font-size:26px;font-family:'Oswald', sans-serif;color:#fff;z-index:1}.home .text-holder:before{content:'';position:absolute;top:-10%;left:-35px;z-index:-1;width:100%;height:80%;background-color:red;border-top:1px solid white;border-left:1px solid white}.home .text-holder:after{content:'';position:absolute;bottom:-10%;right:-35px;z-index:-1;width:100%;height:80%;background-color:rgba(255,0,0,0.5);border-right:1px solid white;border-bottom:1px solid white}

